@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700");

body {
  font-family: "Roboto", sans-serif;
  background: #19202a;
}

@media only screen and (min-width: 320px) {
  header {
    position: absolute;
    z-index: 4;
    padding: 15px;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }
  header .logo {
    width: 50px;
  }
  header .logo img {
    width: 100%;
  }
  header .hamburger-react {
    position: absolute !important;
    top: 15px;
    right: 15px;
    z-index: 20;
  }
  header .hamburger-react > div {
    background-color: white !important;
  }
  header nav.open {
    position: absolute;
    top: 0;
    left: 0;
    /* z-index: 19; */
    text-align: center;
    /* background-image: linear-gradient(
  45deg,#19202a,#19202a 33%,rgba(25,32,42,.96) 36%,rgba(25,32,42,.86) 43%,rgba(176,38,183,.57) 62%,rgba(125,185,235,0)),url(/tekashi69-artist-website/static/media/tekashi.01162399.png); */
    /* background-size: cover; */
    /* filter: blur(50px) saturate(130%); */
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
  }
  header nav.open a {
    color: white;
    text-decoration: none;
    font-weight: 300;
    font-size: 1.2rem;
    padding: 10px 0;
  }
  header nav a {
    color: #fff;
    text-decoration: none;
    font-weight: 300;
    font-size: 1.2rem;
    padding: 0 10px;
  }
  header nav.hamburger-nav-menu {
    background-image: linear-gradient(
        45deg,
        #19202a,
        #19202a 33%,
        rgba(25, 32, 42, 0.96) 36%,
        rgba(25, 32, 42, 0.86) 43%,
        rgba(176, 38, 183, 0.57) 62%,
        rgba(125, 185, 235, 0)
      ),
      url(/tekashi69-artist-website/static/media/tekashi.01162399.png);
    background-size: cover;
    filter: blur(50px) saturate(130%);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 19;
    width: 100%;
    height: 100vh;
  }
  header nav.open .nav-text {
    position: absolute;
    left: 50%;
    z-index: 22;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  #top-section {
    position: relative;
    height: 100vh;
  }
  #top-section .blurredbg {
    background-image: linear-gradient(
        45deg,
        #19202a 0%,
        #19202a 33%,
        rgba(25, 32, 42, 0.96) 36%,
        rgba(25, 32, 42, 0.86) 43%,
        rgba(176, 38, 183, 0.57) 62%,
        rgba(125, 185, 235, 0) 100%
      ),
      url("./assets/images/tekashi.png");
    background-size: cover;
    height: 100vh;
    width: 100%;
    filter: blur(50px) saturate(130%);
    position: absolute;
    z-index: 1;
  }
  #top-section .floating-img {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: end;
    align-items: flex-end;
  }
  #top-section .tekashi-img {
    width: 30%;
  }
  #top-section .overlay {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 3;
    background-color: rgba(0, 0, 0, 0.3);
    width: 100%;
    height: 100%;
  }
  #top-section .tekashi-img {
    /* width: 30%; */
    width: initial;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
  #top-section .tekashi-img img {
    width: 25.063rem;
  }
  #bio {
    background: black;
    position: relative;
    z-index: 3;
    /* padding: 100px 0; */
    padding: 6.25rem 0;
    background-image: linear-gradient(
        45deg,
        #7c4d4d00,
        #c91d1d38 33%,
        rgb(149 13 13 / 23%) 36%,
        rgb(143 108 108 / 0%) 43%,
        rgb(165 48 48 / 26%) 62%,
        rgb(175 102 102 / 34%)
      ),
      url(/tekashi69-artist-website/static/media/logo.451d7fc5.png);
    background-repeat: no-repeat;
    background-position: center;
  }
  #bio .container {
    display: -ms-flexbox;
    display: flex;
  }
  #bio .container .overlay {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.3);
  }
  #bio .container p {
    /* padding: 0 50px; */
    padding: 0px 1.56rem;
    /* width: 50%; */
    width: 100%;
    color: white;
    line-height: 1.8;
    font-size: 0.9rem;
    filter: drop-shadow(5rem 1rem 2rem black) contrast(0.9);
    position: relative;
    /* background-image: linear-gradient( 
45deg, #81464600 0%, #a157578c 33%, rgba(25, 32, 42, 0.96) 36%, rgb(90 53 53 / 59%) 43%, #52303063 62%, rgb(155 64 64 / 31%) 100% ), url(/tekashi69-artist-website/static/media/logo.451d7fc5.png);
    background-repeat: no-repeat;
    background-position: center; */
  }
  #bio .container .logo-centered {
    position: relative;
    display: none !important;
  }

  .container {
    max-width: 1400px;
    margin: 0 auto;
  }
  #media {
    background-image: url("./assets/images/bandana.jpg");
    background-size: cover;
    padding: 100px 0;
  }
  #media .title {
    background: black;
    font-size: 1.1rem;
    font-weight: 300;
    text-transform: uppercase;
    padding: 20px;
    text-align: center;
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    color: white;
  }
  #media .videos {
    padding: 5rem 3.125rem 0; /* equivalent to padding: 80px 50px 0; */
    display: -ms-grid;
    display: grid;
    justify-content: center;
    grid-gap: 2.125rem; /* equivalent to grid-gap: 50px; is 3.125rem */
    padding: 5rem 1.125rem 0;
    grid-template-columns: 100%;
    grid-gap: 2.125rem;
  }
  #media .videos .video {
    border-radius: 4px;
    height: 300px;
    box-shadow: 0 13px 24px 8px rgb(0 0 0 / 75%);
    width: 100%;
    margin: 0 auto;
  }
  #media .videos .video iframe {
    height: 100%;
  }

  #music {
    padding: 80px 0;
    position: relative;
  }
  #music .blurredbg {
    background-image: linear-gradient(
        45deg,
        #fffd3c,
        #fffd3c 33%,
        rgba(255, 253, 60, 0.96) 36%,
        rgba(255, 253, 60, 0.86) 43%,
        rgba(255, 253, 60, 0.2) 62%,
        rgba(125, 185, 232, 0)
      ),
      url(/assets/images/album69.jpg);
    background-size: cover;
    height: 100%;
    width: 100%;
    filter: blur(50px) saturate(130%);
    position: absolute;
    z-index: 1;
  }
  #music .container {
    display: -ms-flexbox;
    display: flex;
    padding: 0 25px;
    position: relative;
    z-index: 3;
  }
  #music .info {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    -ms-flex-align: center;
    align-items: center;
  }
  #music .info .title {
    font-size: 3rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 15px;
  }
  #music .info .sub-title {
    font-size: 1.7rem;
    font-weight: 500;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 12.5rem; /* equivalent to padding-bottom: 400px; */
  }
  #music .info .singles {
    width: 25rem; /* equivalent to width: 400px; */
    display: -ms-grid;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    margin-bottom: 80px;
    margin-top: 50%;
    position: relative;
    top: 5%;
  }
  #music .info li {
    font-size: 1.5rem;
    font-weight: 300;
    text-align: center;
    margin-bottom: 20px;
  }
  #music .info .logos {
    display: -ms-grid;
    display: grid;
    grid-template-columns: repeat(2, minmax(125px, 1fr));
    grid-gap: 20px;
    position: relative;
    top: 0%;
  }
  #music .album-cover {
    width: 100%;
    position: absolute;
    top: 10rem;
    left: 50%;
    transform: translateX(-50%);
  }
  #music .album-cover img {
    width: 80%; /* equivalent to width: 50%; */
    max-width: 20.313rem;
    display: block;
    margin: 0 auto;
  }

  #tour {
    background: black;
    position: relative;
    z-index: 4;
  }
  #tour .top-img {
    background: url("./assets/images/group.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    filter: saturate(0%) contrast(150%);
    height: 300px;
  }
  #tour .tourdates {
    background: black;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-align: center;
    align-items: center;
    padding: 80px 0;
  }
  #tour .tourdates .title {
    color: white;
    text-transform: uppercase;
    font-size: 1.8rem;
    text-align: center;
    margin: 0 0 80px;
  }
  #tour .tourdates .dates {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr 3;
    grid-template-rows: repeat(5, minmax(21px, 1fr));
    grid-auto-flow: column;
    /* grid-template-columns: repeat(3, 1fr); this one doesn't order them in a column */
    grid-gap: 20px;
    padding: 0 20px;
  }
  #tour .tourdates .dates li {
    display: flex;
    justify-content: center;
    column-gap: inherit;
    color: white;
    font-size: 1.3rem;
  }
  #tour .tourdates .dates li span {
    padding: 0 10px;
    padding: 0;
  }
  #tour .tourdates .dates li .city {
    font-weight: 700;
    font-weight: 500;
    font-size: 1rem;
  }
  #tour .tourdates .dates li .location {
    font-size: 1rem;
    font-weight: 300;
  }
  #tour .tourdates .dates li .date {
    font-weight: 500;
    color: #f14848;
    font-size: 1rem;
  }
  #tour .tourdates .pagination {
    margin: 15px auto;
    display: flex;
    list-style: none;
    outline: none;
    padding-top: 20px;
  }
  #tour .tourdates .pagination .page-link {
    border: 2px solid #db3434;
    border-radius: 0.3em;
    color: #fff;
    box-shadow: 0 0 40px 40px #db3434 inset, 0 0 0 0 #db3434;
    transition: all 150ms ease-in-out;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    margin: 10px;
    padding: 0.3em 1em;
  }
  #tour .tourdates .pagination .page-item.active a,
  #tour .tourdates .pagination .page-item a:hover {
    background-color: #c6404000;
    border-color: #de4747;
    color: #fff;
    box-shadow: 0 0 10px 0 #db3434 inset, 0 0 10px 4px #db3434;
  }
  #tour .tourdates .pagination > li > a {
    border-radius: 3px;
    padding: 5px 10px;
    outline: none;
    cursor: pointer;
    text-decoration: none;
  }
  /* #tour .tourdates .pagination li + li:not(:last-child),
  #tour .tourdates .pagination li + li:not(:first-child) {
    padding: 0 5px;
  } */

  footer {
    background: #1b1b1b;
    position: relative;
    z-index: 4;
    padding: 25px;
    text-align: center;
    color: white;
  }
  footer img {
    max-width: 45px;
  }
  footer a {
    color: red;
    text-decoration: none;
  }
  footer div {
    padding-top: 25px;
  }
}
@media only screen and (min-width: 450px) {
  #music .info .singles {
    margin-top: 12.5rem;
  }
}
@media only screen and (min-width: 610px) {
  #music .info .logos {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
  }
  #music .info .logos li {
    width: inherit;
    max-width: 125px;
  }
}
@media only screen and (min-width: 500px) {
  #media .videos {
    grid-gap: 3.125rem;
  }
  #media .videos .video {
    max-width: 464px;
  }
}
@media only screen and (min-width: 520px) {
  #top-section .tekashi-img img {
    /* width: 100%; */
    filter: contrast(150%);
    width: 27rem; /* same as width 100% (432 x 660.578) */
  }
}
@media only screen and (min-width: 700px) {
  #media .videos {
    grid-template-columns: repeat(2, minmax(307px, 1fr));
    grid-gap: 2.125rem;
  }
  #music .info {
    width: 50%;
  }
  #music .info .sub-title {
    margin-bottom: 100px;
  }
  #music .info .singles {
    margin-top: inherit;
    top: inherit;
  }
  #music .info .logos {
    width: auto;
    display: grid;
    grid-template-columns: repeat(2, minmax(125px, 1fr));
  }
  #music .album-cover {
    width: 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    top: auto;
    transform: none;
    left: inherit;
  }
  #music .album-cover img {
    width: auto;
  }
}

@media only screen and (min-width: 1000px) {
  #bio {
    background-image: none;
  }
  #bio .container p {
    padding: 0 50px;
    width: 50%;
  }
  #bio .container .logo-centered {
    width: 50%;
    display: block !important;
  }
  #bio .container .logo-centered img {
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}
@media only screen and (min-width: 1090px) {
  #media .videos {
    grid-template-columns: repeat(2, minmax(300px, 1fr));
  }
  #media .videos .video {
    max-width: 645px;
  }
}
@media only screen and (min-width: 1220px) {
  #music .info .logos {
    grid-template-columns: repeat(4, 1fr);
  }
}


